.simple-button {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 400;
  background-color: #006aff;
  color: white;
  outline: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border: 0;
  border-radius: 50px;
  -webkit-transition: all .15s ease;
  transition: all .15s ease; }
  .simple-button:hover {
    background-color: rgba(0, 106, 255, 0.85);
    color: white; }
  .simple-button:active {
    background-color: #006aff;
    color: white; }
  .simple-button:disabled {
    cursor: default;
    background-color: rgba(0, 106, 255, 0.2); }

.loader {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px; }
  .loader div {
    position: absolute;
    top: 27px;
    width: 11px;
    height: 11px;
    border-radius: 50%;
    background: #006aff;
    -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
            animation-timing-function: cubic-bezier(0, 1, 1, 0); }
    .loader div:nth-child(1) {
      left: 6px;
      -webkit-animation: loader-anim1 0.6s infinite;
              animation: loader-anim1 0.6s infinite; }
    .loader div:nth-child(2) {
      left: 6px;
      -webkit-animation: loader-anim2 0.6s infinite;
              animation: loader-anim2 0.6s infinite; }
    .loader div:nth-child(3) {
      left: 26px;
      -webkit-animation: loader-anim2 0.6s infinite;
              animation: loader-anim2 0.6s infinite; }
    .loader div:nth-child(4) {
      left: 45px;
      -webkit-animation: loader-anim3 0.6s infinite;
              animation: loader-anim3 0.6s infinite; }

@-webkit-keyframes loader-anim1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes loader-anim1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@-webkit-keyframes loader-anim2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  100% {
    -webkit-transform: translate(19px, 0);
            transform: translate(19px, 0); } }

@keyframes loader-anim2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  100% {
    -webkit-transform: translate(19px, 0);
            transform: translate(19px, 0); } }

@-webkit-keyframes loader-anim3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0); } }

@keyframes loader-anim3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0); } }

